.contain {
    padding: 50px;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
} 
.TODOsLoading {
    background: conic-gradient(transparent,rgb(87, 255, 31));
    height: 250px;
    width: 250px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    animation: load 3s linear(0 0%, 1.24 87.49%) infinite;
}
.TODOsLoading::before {
    content: '';
    position: absolute;
    width: 190PX;
    height: 190PX;
    border-radius: 50%;
    background-color: white;
}
@keyframes load {
    100%
        {
            transform: rotate(2000deg);
        }
}

